
body {
  background: #141414;
  color: white;
  font-family:'Times New Roman', Times, serif
}

.container {
  max-width: 250px;
  display: flex;
}

.movie-card {
  margin-top: 50px;
  margin-left: 10px;
  margin-right: 10px;
  transition: transform 0.2s;
}

.movie-card:hover {
  cursor : pointer;
  transform: scale(1.1);
}

.movie-cover {
  width: 100%;
}

.button {
  display: inline-block;
  border-radius: 4px;
  background-color: red;
  border: none;
  color: #FFFFFF;
  text-align: center;
  padding: 10px;
  width: 200px;
  transition: all 0.5s;
  cursor: pointer;
  margin: 5px;
}

.button span {
  cursor: pointer;
  display: inline-block;
  position: relative;
  transition: 0.5s;
}

.button span:after {
  content: '\2665';
  position: absolute;
  opacity: 0;
  top: 0;
  right: -20px;
  transition: 0.5s;
}

.button:hover span {
  padding-right: 25px;
}

.button:hover span:after {
  opacity: 1;
  right: 0;
}

input {
  border-radius: 1em;
  padding: 1em;
}

.youtube {
  max-width: 100%;
}